import React from "react";

const PlayerButton = ({ menuOpen, setmenuOpen }) => {
  return (
    <div
      onClick={() => setmenuOpen((prev) => !prev)}
      className={`media-icon ${
        menuOpen ? "audio-player-icon-open" : "audio-player-icon-close"
      } media-icon"`}
    >
      <span>{"→"}</span>
    </div>
  );
};

export default PlayerButton;
