import React, { useState } from "react";
import Logo from "../../assets/img/logo.png";
import { addVoicenote } from "./Axios";
import Swal from "sweetalert2";

const AudioImport = ({ audioimportOpen, coords, country_name, city_name }) => {
  const [isaudio, setisaudio] = useState();
  const [istitle, setistitle] = useState("");

  const inputHandler = (e) => {
    console.log(e.target.name);
    if (e.target.name === "istitle") {
      setistitle(e.target.value);
    }
    if (e.target.name === "isaudio") {
      if (!(e.target.files.length === 0)) {
        let file = e.target.files[0];
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          // let fileInfo = {
          //   name: file.name,
          //   type: file.type,
          //   size: Math.round(file.size / 1000) + " kB",
          //   base64: reader.result,
          // };
          if (Math.round(file.size / 1000) <= 15000) {
            setisaudio(reader.result);
            if (istitle === "") {
              setistitle(file.name);
            }
          } else {
            setisaudio();
            Swal.fire({
              icon: "error",
              html: `<div class="voicenote-save">Audio size is too big</div>`,
              toast: true,
              position: "top",
              timer: 1000,
              timerProgressBar: false,
              showConfirmButton: false,
            });
          }
        };
      }
    }
  };

  const uploadHandler = async () => {
    console.log(isaudio);
    if (!(isaudio === undefined)) {
      let uploadFile = await addVoicenote(
        city_name,
        country_name,
        coords[0],
        coords[1],
        istitle,
        isaudio
      );
      console.log(uploadFile);
      if (uploadFile.status === "VALID") {
        Swal.fire({
          icon: "success",
          html: `<div class="voicenote-save">Audio was added</div>`,
          toast: true,
          position: "top",
          timer: 1000,
          timerProgressBar: false,
          showConfirmButton: false,
        });
      }
    }
  };
  return (
    <div className={audioimportOpen ? "audioimportOpen" : "audioimportClose"}>
      <div
        style={{
          padding: "10px",
        }}
      >
        <div className="container ">
          <div
            className="text-center"
            style={{ transform: "translateY(-35px)" }}
          >
            <img
              src={Logo}
              alt="Logo for Nocturnal Voices"
              style={{
                height: "50px",
              }}
            />
            <div
              style={{
                fontSize: "15px",
                fontWeight: "bolder",
                color: "#495fef",
              }}
            >
              Whats on your mind?
            </div>
            <input
              type="text"
              name="istitle"
              onChange={(e) => inputHandler(e)}
            />

            <input
              type="file"
              name="isaudio"
              accept="audio/*"
              onChange={(e) => inputHandler(e)}
            />
            <button onClick={() => uploadHandler()} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AudioImport;
